<template>
  <div class="home">
    <el-container>
      <el-header>
        <el-menu
          mode="horizontal"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
          @select="handleSelect"
        >
          <el-menu-item index="1">UniMapView</el-menu-item>
          <el-submenu index="2">
            <template slot="title">基础操作</template>
            <el-submenu index="2-1">
              <template slot="title">切换地图</template>
              <el-menu-item index="2-1-1">ArcGIS</el-menu-item>
              <el-menu-item index="2-1-2">Cesium</el-menu-item>
            </el-submenu>
            <el-menu-item index="2-2">地图放大</el-menu-item>
            <el-menu-item index="2-3">地图缩小</el-menu-item>
            <el-menu-item index="2-4">移动中心点</el-menu-item>
            <el-menu-item index="2-5">重置地图</el-menu-item>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title">地图操作</template>
            <el-submenu index="3-1" v-if="mapConfig.mapType === 'ArcGIS'">
              <template slot="title">切换底图</template>
              <div
                v-for="(item, index) in arcgisBaseMap"
                :key="'arcgis-' + index"
              >
                <el-menu-item
                  :index="'3-1-' + index"
                  v-if="item.id !== 'referenceLayer'"
                  >{{ item.title }}</el-menu-item
                >
              </div>
            </el-submenu>
            <el-submenu index="3-1" v-if="mapConfig.mapType === 'Cesium'">
              <template slot="title">切换底图</template>
              <div
                v-for="(item, index) in cesiumBaseMap"
                :key="'cesium-' + index"
              >
                <el-menu-item
                  :index="'3-1-' + index"
                  v-if="item.id !== 'referenceLayer'"
                  >{{ item.title }}</el-menu-item
                >
              </div>
            </el-submenu>
            <!-- <el-menu-item index="3-2">添加图层</el-menu-item> -->
            <el-menu-item index="3-3">添加点</el-menu-item>
            <el-menu-item index="3-4">添加线</el-menu-item>
            <!-- <el-menu-item index="3-5" v-if="mapConfig.mapType === 'Cesium'">添加3D模型</el-menu-item>
            <el-menu-item index="3-6" v-if="mapConfig.mapType === 'Cesium'">添加倾斜摄影</el-menu-item> -->
          </el-submenu>
        </el-menu>
      </el-header>
      <el-main>
        <div id="container"></div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import * as UnitedMapView from "../../lib/UnitedMapView";

export default {
  name: "homePage",
  components: {},
  data() {
    return {
      mapConfig: {
        mapEl: "container", // 地图容器元素
        mapType: "ArcGIS", // 地图类型 ArcGIS Cesium
        mapZoom: 11, // 地图层级
        mapCenter: [116.3972282409668, 39.90960456049752], // 地图中心点
        mapBaseMap: "topo", // 地图默认底图
      },
      arcgisBaseMap: [
        {
          id: "topo",
          title: "默认",
          url: "https://services.arcgisonline.com/ArcGIS/rest/services/World_Topo_Map/MapServer",
          type: "TileLayer",
        },
        {
          id: "shiliangqiumian",
          title: "天地图-矢量球面",
          url: "https://{subDomain}.tianditu.gov.cn/vec_w/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=vec&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles&TILECOL={col}&TILEROW={row}&TILEMATRIX={level}&tk=b55d3009063383e1a63f3fa139eb506b",
          type: "WebTileLayer",
        },
        {
          id: "yingxiangqiumian",
          title: "天地图-影像球面",
          url: "https://{subDomain}.tianditu.gov.cn/img_w/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=img&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles&TILECOL={col}&TILEROW={row}&TILEMATRIX={level}&tk=b55d3009063383e1a63f3fa139eb506b",
          type: "WebTileLayer",
        },
        {
          id: "referenceLayer",
          title: "天地图-矢量标注",
          url: "https://{subDomain}.tianditu.gov.cn/cva_w/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=cva&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles&TILECOL={col}&TILEROW={row}&TILEMATRIX={level}&tk=b55d3009063383e1a63f3fa139eb506b",
          type: "WebTileLayer",
        },
      ],
      cesiumBaseMap: [
        {
          id: "topo",
          title: "默认",
          url: "https://services.arcgisonline.com/ArcGIS/rest/services/World_Topo_Map/MapServer",
          type: "ArcGisMapServerImageryProvider",
        },
        {
          id: "shiliangqiumian",
          title: "天地图-矢量球面",
          url: "https://{subDomain}.tianditu.gov.cn/vec_w/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=vec&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles&TILECOL={col}&TILEROW={row}&TILEMATRIX={level}&tk=b55d3009063383e1a63f3fa139eb506b",
          type: "WebMapTileServiceImageryProvider",
        },
        {
          id: "yingxiangqiumian",
          title: "天地图-影像球面",
          url: "https://{subDomain}.tianditu.gov.cn/img_w/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=img&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles&TILECOL={col}&TILEROW={row}&TILEMATRIX={level}&tk=b55d3009063383e1a63f3fa139eb506b",
          type: "WebMapTileServiceImageryProvider",
        },
        {
          id: "referenceLayer",
          title: "天地图-矢量标注",
          url: "https://{subDomain}.tianditu.gov.cn/cva_w/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=cva&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles&TILECOL={col}&TILEROW={row}&TILEMATRIX={level}&tk=b55d3009063383e1a63f3fa139eb506b",
          type: "WebMapTileServiceImageryProvider",
        },
      ],
      pointList: [
        {
          center: [116.500997, 39.873927],
          img: require("../../assets/hot-air-balloon.png"),
          attributes: {
            id: "huanlegu",
            name: "北京欢乐谷",
            desc: "地址：北京市朝阳区东四环小武基北路",
          },
        },
        {
          center: [116.254886, 39.891152],
          img: require("../../assets/hot-air-balloon.png"),
          attributes: {
            id: "huanleshuimofang",
            name: "北京欢乐水魔方水上乐园",
            desc: "地址：北京市丰台区小屯路11号",
          },
        },
        {
          center: [116.104308, 39.848027],
          img: require("../../assets/hot-air-balloon.png"),
          attributes: {
            id: "lvyexianzong",
            name: "绿野仙踪郊野乐园",
            desc: "地址：北京市丰台区王佐镇西庄店甲2号",
          },
        },
      ],
      lineList: [
        {
          attributes: {
            id: "jhgs3-5",
            name: "京沪高速三环至五环",
          },
          color: "rgba(162,98,250,1)",
          paths: [
            [
              [116.461817, 39.868685],
              [116.485245, 39.848191],
              [116.511691, 39.825919],
            ],
          ],
        },
        {
          attributes: {
            id: "jcgs3-5",
            name: "机场高速三环至五环",
          },
          color: "rgba(162,98,250,1)",
          paths: [
            [
              [116.461314, 39.963648],
              [116.476262, 39.977361],
              [116.506732, 40.005553],
            ],
          ],
        },
      ],
    };
  },
  mounted() {
    this.initMapView();
  },
  methods: {
    // 初始化地图
    initMapView() {
      if (this.mapConfig.mapType === "ArcGIS") {
        UnitedMapView.initMapView(this.mapConfig, this.arcgisBaseMap);
      } else if (this.mapConfig.mapType === "Cesium") {
        UnitedMapView.initMapView(this.mapConfig, this.cesiumBaseMap);
      }
    },
    // 销毁地图
    destroyMapView() {
      UnitedMapView.destroyMapView();
    },
    // 地图操作
    handleSelect(key, keyPath) {
      // 切换地图
      if (keyPath[1] === "2-1") {
        this.destroyMapView();
        if (key === "2-1-1") {
          if (this.mapConfig.mapType !== "ArcGIS") {
            this.mapConfig.mapType = "ArcGIS";
          }
        }
        if (key === "2-1-2") {
          if (this.mapConfig.mapType !== "Cesium") {
            this.mapConfig.mapType = "Cesium";
          }
        }
        this.initMapView();
      }
      // 切换底图
      if (keyPath[1] === "3-1") {
        let baseMaps =
          this.mapConfig.mapType === "ArcGIS"
            ? this.arcgisBaseMap
            : this.cesiumBaseMap;
        if (key === "3-1-0") {
          UnitedMapView.changeBaseMap(baseMaps[0]);
        }
        if (key === "3-1-1") {
          UnitedMapView.changeBaseMap(baseMaps[1]);
        }
        if (key === "3-1-2") {
          UnitedMapView.changeBaseMap(baseMaps[2]);
        }
      }
      // 地图放大
      if (keyPath[1] === "2-2") {
        UnitedMapView.mapZoomIn();
      }
      // 地图缩小
      if (keyPath[1] === "2-3") {
        UnitedMapView.mapZoomOut();
      }
      // 地图移动
      if (keyPath[1] === "2-4") {
        UnitedMapView.mapCenterMove([121.480248, 31.236276]);
      }
      // 地图重置
      if (keyPath[1] === "2-5") {
        UnitedMapView.mapReset();
      }
      // 添加图层
      if (keyPath[1] === "3-2") {
        UnitedMapView.mapReset();
      }
      // 添加点图层
      if (keyPath[1] === "3-3") {
        UnitedMapView.addPoint("leyuan", this.pointList);
      }
      // 添加线图层
      if (keyPath[1] === "3-4") {
        UnitedMapView.addLine("gaosu", this.lineList);
      }
    },
  },
};
</script>

<style scoped lang="scss">
.home {
  width: 100%;
  height: 100%;

  #container {
    width: 100%;
    height: calc(100vh - 60px);
    border: none !important;
  }

  .el-header,
  .el-main {
    margin: 0;
    padding: 0;
  }
}
</style>
